import {
  Badge,
  Box,
  Divider,
  Grid,
  GridJustification,
  Icon,
} from "@material-ui/core";
import React, { ReactElement, useState } from "react";
import { useTranslation } from "react-i18next";
import { TopMenuType } from "estreeh-common-library/src/types/TopMenuType";
import { slide as Menu, State } from "react-burger-menu";
import Branding from "../../config/Branding";
import Logo from "../common/Logo";
// import Faker from "faker";
import TopMenuItem from "../common/TopMenuItem";
import BurgerItem from "../common/BurgerItem";
import { ITopNavItem } from "estreeh-common-library/src/interfaces/ITopNavItem";
// import axios from "axios";
import { navigate, useLocation } from "@reach/router";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";

interface Props {
  isBurger: boolean;
  burgerWidth?: number;
  burgerLeft?: number;
  burgerTop?: number;
  justify?: GridJustification;
}

export default function TopMenu(props: Props): ReactElement {
  const { t, i18n } = useTranslation();
  const isEnglish = i18n.language === "en";
  const langTitle = isEnglish ? "العربية" : "English";
  const targetLang = isEnglish ? "ar" : "en";
  const location = useLocation();
  const favorites = useStoreState((s) => s.favTwoYear.items);

  const burgerWidth = props.burgerWidth ? props.burgerWidth : 26;
  const burgerTop = props.burgerTop ? props.burgerTop : 26;
  const burgerLeft = props.burgerLeft ? props.burgerLeft : 24;
  const user = useStoreState((state) => state.user.item);
  const requests = useStoreState(s => s.requests.items);
  const returnUrl = useStoreState((state) => state.returnUrl);
  const setReturnUrl = useStoreActions((action) => action.returnUrl.setItem);

  const myRequestsUrl = "/my/requests";

  const visibleFavorites = favorites.filter((x) => !x.hidden && !x.collected);
  const countRequests = requests.length;

  const [isOpen, setIsOpen] = useState(false);

  const handleStateChange = (state: State) => {
    setIsOpen(state.isOpen);
  };

  const loggedIn = user?.fullName !== undefined;

  const burgerStyles = {
    bmBurgerButton: {
      position: "absolute",
      width: burgerWidth + "px",
      height: "20px",
      left: burgerLeft + "px",
      top: burgerTop + "px",
    },
    bmBurgerBars: {
      background: "#373a47",
    },
    bmBurgerBarsHover: {
      background: "#a90000",
    },
    bmCrossButton: {
      height: "24px",
      width: "24px",
    },
    bmCross: {
      background: Branding.primary,
    },
    bmMenuWrap: {
      position: "fixed",
      height: "100%",
    },
    bmMenu: {
      background: Branding.lightBg + "f4",
      padding: "0.5em 1.5em 0",
      fontSize: "1.15em",
    },
    bmMorphShape: {
      fill: "#373a47",
    },
    bmItemList: {
      color: "#b8b7ad",
      padding: "0.8em",
      height: "auto",
    },
    // bmItem: {
    //   display: "block",
    //   marginBottom: 16,
    // },
    bmOverlay: {
      background: "rgba(0, 0, 0, 0.3)",
    },
  };

  const loginLink: ITopNavItem = {
    title: t("login"),
    // to: "/login",
    onClick: () => {
      if (returnUrl === undefined) {
        setReturnUrl(location.pathname);
      }
      navigate("/login");
    },
    prominent: true,
  };

  const profileLink: ITopNavItem = {
    title: "Welcome, " + user?.fullName + "!",
    to: myRequestsUrl,
    // prominent: true,
  };

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const langLink: ITopNavItem = {
    title: langTitle,
    onClick: () => {
      i18n.changeLanguage(targetLang);
    },
  };

  const favLink: ITopNavItem = {
    to: "/my/favorites",
    node: (
      <Badge
        // badgeContent={Faker.random.number({ min: 1, max: 9 })}
        badgeContent={visibleFavorites.length}
        color="primary"
      >
        <Icon fontSize="small">favorite_outlined</Icon>
      </Badge>
    ),
  };

  const myRequestsLink: ITopNavItem = {
    title: t("my-requests"),
    to: myRequestsUrl,
    count: countRequests,
    badgeOverlap: "circle",
  };

  const contactUsLink: ITopNavItem = {
    title: t("contact-us"),
    to: "/contact-us",
  };

  //FUTURE: Re-enable language select button

  const topMenuNotLoggedIn: TopMenuType = [
    loginLink,
    favLink,
    // myRequestsLink,
    // {
    //   title: t("faq"),
    // },
    contactUsLink,
    // langLink,
  ];

  const topMenuLoggedIn: TopMenuType = [
    profileLink,
    favLink,
    myRequestsLink,
    contactUsLink,
    // langLink,
    // logoutLink,
  ];

  const topMenu = loggedIn ? topMenuLoggedIn : topMenuNotLoggedIn;

  return (
    <React.Fragment>
      {props.isBurger && (
        <Menu
          styles={burgerStyles}
          pageWrapId={"page-content-container"}
          outerContainerId={"app-root"}
          isOpen={isOpen}
          onStateChange={(state) => handleStateChange(state)}
          disableAutoFocus={true}
        >
          <Box>
            <Logo marginLeft={0} maxHeight={40} />
          </Box>
          <Box pt={2} pb={1}>
            <Divider />
          </Box>
          {topMenu.map((nav) => (
            <BurgerItem
              key={nav.title + "_" + nav.to}
              item={nav}
              setIsOpen={setIsOpen}
            />
          ))}
        </Menu>
      )}
      {!props.isBurger && (
        <Grid
          container
          justify={props.justify}
          direction="row-reverse"
          alignItems="center"
        >
          {topMenu.map((nav) => (
            <Grid key={nav.title + "_" + nav.to} item>
              <TopMenuItem item={nav} />
            </Grid>
          ))}
        </Grid>
      )}
    </React.Fragment>
  );
}
