import { Box, Checkbox, Link } from "@material-ui/core";
import { Grid } from "@material-ui/core";
import { ReactElement } from "react";

interface Props {
  title: string;
  verb?: string;
  labelHref?: string;
  labelOnClick?: any;
  checkboxOnClick?: any;
}

export default function TandCItem(props: Props): ReactElement {
  const verb = props.verb ?? "I have read & accepted";

  return (
    <Box fontSize="95%">
      <Grid container alignItems="center">
        <Grid item xs={2} md={1}>
          <Checkbox color="primary" />
        </Grid>
        <Grid item xs={10} md={11}>
          <Box>
            {verb} the{" "}
            <Link
              href={props.labelHref}
              style={{
                cursor: "pointer",
                userSelect: "none",
              }}
            >
              {props.title}
            </Link>
            .
          </Box>
        </Grid>
      </Grid>
    </Box>
  );
}
