import { Box, Grid, Typography } from "@material-ui/core";
import { RouteComponentProps } from "@reach/router";
import React, { ReactElement, useEffect, useState } from "react";
// import { UserFactory } from "../../factories/UserFactory";
import NiceBox from "../common/NiceBox";
import RequestGridCard from "./RequestGridCard";
import _ from "lodash";
import { IRequest } from "estreeh-common-library/src/interfaces/IRequest";
import axios from "axios";
import { IResponse } from "estreeh-common-library/src/interfaces/IResponse";
import { useStoreActions, useStoreState } from "../../helpers/StoreTypedHooks";

interface Props extends RouteComponentProps {}

export default function MyRequestsPage(props: Props): ReactElement {
  const user = useStoreState((s) => s.user.item);
  const requests = useStoreState((s) => s.requests.items);
  const setRequests = useStoreActions((a) => a.requests.setItems);

  // const [requests, setRequests] = useState<IRequest[]>([]);
  const [loaded, setLoaded] = useState(false);

  useEffect(() => {
    const getRequests = async () => {
      return await axios.get<IResponse<IRequest[]>>(
        process.env.REACT_APP_API_ROOT + "/customer/requests",
        { withCredentials: true }
      );
    };
    if (!loaded) {
      console.log("Getting requests");
      setLoaded(true);
      getRequests()
        .then((response) => {
          const d = response.data.data;
          setRequests({ payload: _.orderBy(d, ["status.code"]) });
        })
        .catch((reason) => {
          console.log(reason);
        });
    }
    return () => {};
  }, [user, setRequests, loaded, setLoaded]);

  // TODO: Create new UI for the "not logged in user"
  // TODO: Either by Tracking Code, or by mobile + first 4 letters of name (or less)

  return (
    <Box>
      <Box py={2}>
        <Typography variant="h4">My Requests</Typography>
      </Box>
      <Typography variant="h6">
        Logged in as <strong>{user?.fullName}</strong>
      </Typography>
      <NiceBox>
        <Grid container spacing={1}>
          {requests.map((request) => (
            <RequestGridCard request={request} key={request._id} />
          ))}
        </Grid>
      </NiceBox>
    </Box>
  );
}
