import { Avatar, Box, Grid, Tab, Tabs, Typography } from "@material-ui/core";
import React from "react";
import IAgency from "estreeh-common-library/src/interfaces/IAgency";
import AgencyRatingInfo from "./AgencyRatingInfo";
import NiceBox from "../common/NiceBox";
import AgencyTabPanels from "./AgencyTabPanels";
import IAgencyTab from "estreeh-common-library/src/interfaces/IAgencyTab";
import { makeCountryChipsFromAgencyTabs } from "./AgencyCard";
import { useTranslation } from "react-i18next";
import CountryFlag from "../common/CountryFlag";

const AgencyDetails = (props: {
  agency: IAgency;
  showTwoYearTabs: boolean;
  showRentalTabs: boolean;
}) => {
  const { t } = useTranslation();
  const [value, setValue] = React.useState(0);

  const handleTabChange = (event: React.ChangeEvent<{}>, newValue: any) => {
    setValue(newValue);
  };

  const agency = props.agency;

  let tabs: IAgencyTab[];

  if (props.showTwoYearTabs) {
    tabs = agency.tabsTwoYearWorkers;
  } else {
    // tabs = [...agency.tabsRental, ...agency.tabsServices];
    tabs = [...agency.tabsRental];
  }

  const countryChips = makeCountryChipsFromAgencyTabs(tabs, t);

  // FIXME: "Sri Lanka" shows in 2 lines in mobile responsive devices
  // FIXME: When changing filters the tab index misbehaves
  const tabNavigationButtons = countryChips.map((country) => (
    <Tab
      label={
        <CountryFlag
          country={country}
          showFees={false}
          height={18}
          marginRight={8}
        />
      }
      style={{
        fontWeight: "bold",
        textTransform: "none",
        flexDirection: "row",
      }}
      classes={{
        wrapper: "no-flex-direction",
      }}
      key={country.country}
    />
  ));

  let tabsIndex = -1;
  const tabPanels = tabs.map((tab) => {
    tabsIndex++;
    return (
      <AgencyTabPanels
        value={value}
        tab={tab}
        index={tabsIndex}
        design="grid"
        key={tab.title}
      />
    );
  });

  return (
    <NiceBox>
      <Grid container spacing={2}>
        <Grid item md={3} xs={12}>
          <Avatar
            variant="rounded"
            src={process.env.REACT_APP_IMAGES_ROOT + "" + agency.logo}
            style={{
              flexShrink: 0,
              width: "100%",
              minHeight: 100,
            }}
          />
        </Grid>
        <Grid item md={9} xs={12}>
          <Typography variant="h5">{agency.title}</Typography>
          <Box>
            <AgencyRatingInfo agency={agency} />
            {/* <AgencyContactInfo agency={agency} /> */}
          </Box>
        </Grid>
        <Grid item xs={12}>
          <Box
            display={
              tabNavigationButtons.length < 2 && props.showRentalTabs
                ? "none"
                : "block"
            }
          >
            <Tabs
              value={value}
              onChange={handleTabChange}
              indicatorColor="primary"
              scrollButtons="on"
              variant="scrollable"
            >
              {tabNavigationButtons}
            </Tabs>
          </Box>
          {tabPanels}
        </Grid>
      </Grid>
    </NiceBox>
  );
};

export default AgencyDetails;
